<template>
  <div class="login">
    <van-nav-bar title="登录" left-text="注册" right-text="关闭"
     @click-left="goPage('Register')"
     @click-right="goPage('Home')"/>
    <div class="logo">
      <img class="auto-img" src="../assets/img/logo.jpg" alt />
    </div>
    <!-- From + Field  -->
    <van-form @submit="login">
      <van-field
        v-model="userinfo.phone"
        type="text"
        label="手机号"
        placeholder="手机号"
        left-icon="phone-o"
        maxlength="11"
      >
        <!-- 自定义右边图标 -->
        <!-- <template v-slot:right-icon>
          <i class="fa fa-camera-retro fa-lg"></i>
        </template>-->
      </van-field>
      <van-field
        v-model="userinfo.password"
        :type="iseye ? 'text' : 'password'"
        label="密码"
        placeholder="密码"
        left-icon="lock"
        :right-icon="iseye ? 'eye-o' : 'closed-eye'"
        @click-right-icon="passwords"
        maxlength="16"
      />
      <div class="login-btn">
        <van-button round block type="info" native-type="submit">立即登录</van-button>
        <div class="forgot">
          <span class="fr">忘记密码？</span>
        </div>
      </div>
    </van-form>
  </div>
</template>

<script>
import validFrom from "../assets/js/vaidfrom";

export default {
  data() {
    return {
      userinfo: {
        phone: "",
        password: ""
      },
      iseye: false
    };
  },
  methods: {
    //跳转到登录页面
    goPage(name) {
      this.$router.push({ name });
    },
    //切换密码的格式 自定义事件
    passwords() {
      this.iseye = !this.iseye;
    },

    //注册  前端先验证
    login() {
      
      if (validFrom.validUserFrom(this.userinfo)) {
        //复制对象  把v-model 取消数据双向
        let data = Object.assign({}, this.userinfo);

        //请求密钥
        data.appkey = this.appkey;

        //开启加载提示
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,

          //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
          duration: 0
        });

        this.axios({
          method: "POST",
          url: "/login",
          //post请求用  data   get请求用  params
          data
        })
          .then(result => {
            this.$toast.clear();
            // 
            if (result.data.code == 200) {
              //跳转到菜单
              

              //存储token，用于后面登录验证
              localStorage.setItem("_TNK", result.data.token);

              this.$router.push({name:"My"})
            } else {
              this.$toast(result.data.msg);
            }
          })
          .catch(err => {
            this.$toast.clear();
            // 
          });
      }
    }
  }
};
</script>

<style lang="less" scoped>
.login {
  background: #ffffff;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  /deep/.van-nav-bar__text {
    color: #fa8072;
  }
  /deep/ .van-button--info {
    background-color: #fa8072;
    border-color: #fa8072;
  }
  .logo {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    overflow: hidden;
    border: 1px solid #fa8072;
    margin: 50px auto;
  }
  .login-btn {
    margin-top: 40px;
    padding: 0 20px;
  }
  .forgot {
    margin-top: 20px;
  }
}
</style>